import React, { useState, useRef } from "react";
import { Box, IconButton, Typography } from "@mui/material";
import TestimonialCard from "./testimonialCard";
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
const Section7: React.FC = () => {


  const testimonials = [
    {
      id: 1,
      name: "Rohith Vallampalli",
      university: "University of Greenwich",
      message:
        "Choosing One Window for my master's in the USA was the best decision I made. Their guidance was invaluable!",
      image: "https://via.placeholder.com/150",
    },
    {
      id: 2,
      name: "Sai Deep Manthenna",
      university: "University of Greenwich",
      message:
        "Thanks to the amazing team at One Window, I fulfilled my dream of studying in the UK.",
      image: "https://via.placeholder.com/150",
    },
    {
      id: 3,
      name: "Mayah",
      university: "Al Azhar University",
      message:
        "I can't thank One Window enough for helping me make my education dreams come true!",
      image: "https://via.placeholder.com/150",
    },
  ];


  const SectionFour: React.FC<{ cards: any[] }> = ({ cards }) => {
    const [selectedCard, setSelectedCard] = useState(0);
    const containerRef = useRef<HTMLDivElement | null>(null);

    const handleNavigation = (direction: "prev" | "next") => {
      const newIndex =
        direction === "prev"
          ? Math.max(selectedCard - 1, 0)
          : Math.min(selectedCard + 1, testimonials.length - 1);

      setSelectedCard(newIndex);

      // Scroll to the selected card
      if (containerRef.current) {
        const cardWidth = containerRef.current.children[0].clientWidth + 16; // Card width + gap
        containerRef.current.scrollTo({
          left: cardWidth * newIndex,
          behavior: "smooth",
        });
      }
    };

    return (
      <Box position="relative" width="100%">
        <Box sx={{ display: "flex", justifyContent: "end" }}>
          <IconButton
            sx={{
              transform: "translateY(-50%)",
              opacity: selectedCard === 0 ? 0.4 : 1,
              pointerEvents: selectedCard === 0 ? "none" : "auto",
              transition: "opacity 0.3s",
            }}
            onClick={() => handleNavigation("prev")}
            disabled={selectedCard === 0}
          >
            <KeyboardArrowLeftIcon
              sx={{
                color: selectedCard === 0 ? "#ccc" : "#3b3f76",
                border: "1px solid",
                borderColor: selectedCard === 0 ? "#ccc" : "#3b3f76",
                borderRadius: "5px",
              }}
            />
          </IconButton>

          <IconButton
            sx={{
              transform: "translateY(-50%)",
              opacity: selectedCard === testimonials.length - 1 ? 0.4 : 1,
              pointerEvents: selectedCard === testimonials.length - 1 ? "none" : "auto",
              transition: "opacity 0.3s",
            }}
            onClick={() => handleNavigation("next")}
            disabled={selectedCard === testimonials.length - 1}
          >
            <KeyboardArrowRightIcon
              sx={{
                color: selectedCard === testimonials.length - 1 ? "#ccc" : "#3b3f76",
                border: "1px solid",
                borderColor: selectedCard === testimonials.length - 1 ? "#ccc" : "#3b3f76",
                borderRadius: "5px",
              }}
            />
          </IconButton>
        </Box>
        <Box
          ref={containerRef}
          sx={{
            display: "flex",
            gap: 2,
            overflowX: "auto",
            scrollBehavior: "smooth",
            padding: 2,
            scrollbarWidth: "none"
          }}
        >
          {testimonials.map((testimonial, index) => (
            <TestimonialCard
              key={testimonial.id}
              data={testimonial}
              isSelected={index === selectedCard}
            />
          ))}
        </Box>



      </Box>
    );
  };

  return (
    <section style={{
      display: "flex",
      flexDirection: "column",
      justifyContent: "center", alignItems: "center", marginTop: "2rem"
    }}>
      <Box sx={{
        height: "100%",
        maxWidth: "1440px",
        width: "100%",
        position: "relative"
      }}>
        <Box sx={{ width: "100%" }}>
          <Box sx={{ display: "flex", gap: "", justifyContent: "center", alignItems: "center", flexDirection: "column" }}>
            <Box>
              <Typography
                textAlign="center"
                color="#3b3f76"
                fontWeight="500"
                sx={{ fontSize: { xs: "1rem", sm: "2.5rem" } }}
              >
                Testimonials
              </Typography>
              <Typography color="#30336380" textAlign="center">
                Check out our studentâ€™s success stories!
              </Typography>
            </Box>
            {/* <Box
              sx={{
                width: '3rem',
                height: '1.5rem',
                backgroundColor: '#FAEAD0',
                borderRadius: '100px 100px 0 0',
                // display: 'inline-block',
                transform: "rotateZ(240deg)",
                display: "flex", justifyContent: "start",
                m: 3
              }}
            /> */}
          </Box>
          <Box sx={{ padding: { xs: "2.5rem", lg: "3rem 10rem" } }}>
            <SectionFour cards={testimonials} />
          </Box>
        </Box>
      </Box>
    </section>
  );
};

export default Section7;
