import { Box, Button, Grid, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import PhoneIcon from '@mui/icons-material/Phone';
import { setPopup } from "../../../store/Slices/popupSlice";
import { useAppDispatch } from "../../../assets/hooks";
import { useNavigate } from "react-router-dom";
import Images from "src/assets";
import { globalButton, homepageStyles, secondaryButton } from "src/components/styles/customStyles";

const Section1 = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const words = ["Studying Abroad", "Admissions", "Education Loan", "Visa"];
  const [currentWordIndex, setCurrentWordIndex] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentWordIndex((prevIndex: any) => (prevIndex + 1) % words.length);
    }, 2000);

    return () => clearInterval(intervalId);
  }, [words.length]);

  const data = [
    {
      desc: "Quick Admissions",
      icon: Images.discipline
    },
    {
      desc: "1200 Universities over 70,000 + Courses",
      icon: Images.universityType
    },
    {
      desc: "Top universities in 11+ Countries",
      icon: Images.country
    },
    {
      desc: "Data driven Unbiased recommendations",
      icon: Images.ai
    },

  ]
  return (
    <Box sx={homepageStyles.container}>
      <Box sx={homepageStyles.innerContainer}>
        <Grid container spacing={2} sx={homepageStyles.gridContainer}>
          <Grid item xs={12} sx={homepageStyles.imgContainer}>
            <Box component="img" src={Images.homepage1} alt="" sx={homepageStyles.img} />
          </Grid>
          <Grid item xs={12} sm={7}>
            <Box sx={homepageStyles.textContainer}>
              <Typography sx={homepageStyles.headingText}>We make</Typography>
              <Typography key={currentWordIndex} sx={homepageStyles.animatedText}>
                {words[currentWordIndex]}
              </Typography>
              <Typography sx={homepageStyles.headingText}>easier</Typography>
              <Typography sx={homepageStyles.subText}>
                Say goodbye to consultants and compromises. With Advisor Intelligence, our <b style={{color:"#3b3f76"}}>DIY</b> 
                <span style={{fontFamily: "Arimo Hebrew Subset Italic",marginLeft:"2px",color:"#3b3f76", fontStyle: "italic"}}>
                  Do it Yourself &nbsp;
                </span>
                university shortlisting tool, find the universities that best fit your goals
              </Typography>
              <Grid container spacing={1} sx={{ mb: 4 }}>
                {data?.map((item, i) => (
                  <Grid item xs={12} md={6} sx={homepageStyles.gridItem} key={i}>
                    <img src={item.icon} alt="icon" style={homepageStyles.icon} />
                    <Typography sx={{ color: "#30336380", fontSize: "14px" }}>{item.desc}</Typography>
                  </Grid>
                ))}
              </Grid>
              <Box sx={homepageStyles.buttonContainer}>
                <Button  
                  sx={[globalButton, { fontSize: { lg: "16px", xs: "12px" }, width: { lg: "200px", xs: "180px" }, padding: { lg: "8px", xs: "6px" } }]}
                  onClick={() => {
                  const subDisUrl = `activeTab=${JSON.stringify("courses")}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`
                  const subDisciUrl = window.btoa(subDisUrl);
                  navigate(`/explore?${subDisciUrl}`);
                }}>
                  Start shortlisting
                </Button>
                <Button
                  sx={[secondaryButton, { fontSize: { lg: "16px", xs: "12px" }, width: { lg: "200px", xs: "180px" }, padding: { lg: "8px", xs: "6px" }, display: "flex", gap: "5px" }]}
                  onClick={() => {
                  dispatch(setPopup({
                    show: true,
                    data: {
                      container: { name: "callback", dimensions: { width: "500px" } },
                      type: "custom",
                    },
                  }));
                }}>
                  Talk to Expert
                  <PhoneIcon fontSize="small" />
                </Button>
              </Box>
              <Box sx={homepageStyles.appContainer}>
                <Typography sx={homepageStyles.appText}>Download our App for Easy Management</Typography>
                <Box sx={homepageStyles.appIconsContainer}>
                  <img src={Images.appStore} alt="app" style={homepageStyles.appIcon} onClick={() =>window.open("https://apps.apple.com/in/app/one-window/id6670320345", "_blank")}/>
                  <img src={Images.googlePlay} alt="app" style={homepageStyles.appIcon} onClick={() => window.open("https://play.google.com/store/apps/details?id=com.onewindow.app&pcampaignid=web_share", "_blank")}/>
                </Box>
              </Box>
            </Box>
          </Grid>
          <Grid item sm={5} sx={{ display: { xs: "none", sm: "flex" }, justifyContent: "end", alignItems: "center" }}>
            <Box component="img" src={Images.homepage1} alt="" sx={homepageStyles.homeImage} />
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default Section1;
