import { Box, Typography } from "@mui/material";
import CustomStepper from "./customStepper";
import { Image1 } from "./customImages";
import { useEffect, useState, useRef } from "react";
import { keyframes } from "@mui/system";

const Section2 = () => {
  const [display, setDisplay] = useState(false);
  const [inView, setInView] = useState(false); 
  const sectionRef = useRef(null); 

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true); 
        } else {
          setInView(false); 
        }
      },
      {
        threshold: 0.5, 
      }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      if (sectionRef.current) {
      // eslint-disable-next-line
        observer.unobserve(sectionRef.current);
      }
    };
  }, []);

  useEffect(() => {
    setTimeout(() => {
      if(!inView)
      setDisplay(true)
    }, 4000);
  },[inView])

  const moveFromRightBottomToCenterAndRight = keyframes`
    0% {
      transform: translate(50%, 50%);
      opacity: 0;
    }
    50% {
      transform: translate(0, 0);
      opacity: 1;
    }
    100% {
      transform: translateX(300px);
    }
  `;

  return (
    <Box
      ref={sectionRef} 
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: {lg:"650px"},
      }}
    >
      <Box
        sx={{
          height: "100%",
          maxWidth: "1440px",
          width: "100%",
          position: "relative",
        }}
      >
        <Typography
          textAlign="center"
          color="#3b3f76"
          fontWeight="500"
          sx={{ fontSize: { md: "2.5rem", xs: "1.8rem" }, my: 3 }}
        >
          How Does This <span style={{ color: "#30336380" }}>Work</span>?
        </Typography>
        {display ? (
          <CustomStepper />
        ) : (
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "480px",
            }}
          >
            {inView  && ( 
              <Box
                sx={{
                  animation: `${moveFromRightBottomToCenterAndRight} 2s ease forwards`,
                }}
              >
                <Image1 />
              </Box>
            )}
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default Section2;