import { Box, Grid, Typography } from "@mui/material";
import React, { useState } from "react";
import Images from "src/assets";
import { Image1, Image2, Image3, Image4 } from "./customImages";
import { homepageStyles } from "src/components/styles/customStyles";

const steps = [
  {
    title: "Sign Up",
    description: "Create your account by signing up to start your journey.",
    image: <Image1 />
  },
  {
    title: "Complete Your Profile",
    description:
      "Share your academic background, extracurriculars and preferences for personalized results.",
    image: <Image2 />

  },
  {
    title: "Generate Recommendations",
    description:
      "Instantly access university matches perfectly aligned with your goals.",
    image: <Image3 />

  },
  {
    title: "Apply & Get Admissions",
    description:
      "Choose your top picks to apply and take the next step toward acceptance.",
    image: <Image4 />

  },
];

const CustomStepper = () => {
  const [activeStep, setActiveStep] = useState(0);
  const handleStepClick = (step: number) => {
    setActiveStep(step);
  };
  return (
    <Grid container spacing={2} sx={{ m: { xs: 0, md: 2 } }}>
      <Grid item xs={12} md={7} sx={{ display: {md:"none",xs:"flex"}, justifyContent: "center", alignItems: "center",mt:2 }}>
          {steps[activeStep].image}
      </Grid>
      <Grid item xs={12} md={5} sx={{ pl: 3 }}>
        <div style={{ position: "relative", padding: "20px" }}>
          <Box sx={homepageStyles.gifIcon}></Box>
          {steps.map((step, index) => (
            <div
              key={index}
              style={{
                display: "flex",
                alignItems: "center",
                marginBottom: "40px",
                position: "relative",
                cursor: "pointer"
              }}
              onClick={() => handleStepClick(index)}
            >
              {activeStep === index && (
                <img src={Images.activeIcon} alt="icon" className="shaking-image"  />
              )}

              <div style={{ marginLeft: "50px" }}>
                <Typography
                  sx={{
                    fontSize: activeStep === index ? "20px" : "18px",
                    fontWeight: activeStep === index ? "bold" : "normal",
                    color: activeStep === index ? "#3b3f76" : "#303363d6",
                    margin: "0 0 5px",
                  }}
                >
                  {step.title}
                </Typography>
                <p
                  style={{
                    fontSize: activeStep === index ? "16px" : "14px",
                    color: activeStep === index ? "#3B3F76" : "rgb(59 63 118 / 62%)",
                    margin: 0,
                  }}
                >
                  {step.description}
                </p>
              </div>
            </div>
          ))}
        </div>
      </Grid>
      <Grid item xs={12} md={7} sx={{ display: {md:"flex",xs:"none"}, justifyContent: "center", alignItems: "center" }}>
        <Box>
          {steps[activeStep].image}
        </Box>
      </Grid>

    </Grid>

  );
};

export default CustomStepper;
